.tab {
	&__filter {
		display: flex;
		justify-content: space-between;
		max-width: 50%;
		margin: 0 auto 40px;
		border-radius: 4px; }
	&__link {
		padding: 10px 0;
		border: 1px solid transparentize($violet, .8);
		border-radius: 4px;
		background-color: $white;
		box-shadow: 0 0 10px rgba(0,0,0,.1);
		width: 30%;
		font-size: 14px;
		font-weight: 600;
		text-transform: capitalize;
		letter-spacing: 0.9px;
		color: $violet;
		@include trans(transform);
		&:hover {
			box-shadow: 0 2px 5px rgba(0,0,0,.3);
			transform: translate(0, -3px); }
		&.is-active {
			background-color: $violet;
			color: $white; } }
	&__content {
		display: none;
		background-color: $white;
		border: 1px solid transparentize($violet, .8);
		border-radius: 4px;
		min-height: 450px;
		&.is-active {
			display: flex;
			justify-content: space-between; } } }



.adv-list {
	background: $violet;
	color: $white;
	width: 30%;
	padding: 20px 30px;
	min-height: 100%;
	h4 {
		font-size: 26px;
		text-align: center;
		font-weight: 500;
		letter-spacing: 0.5px;
		margin-bottom: 20px;
		color: $yellow; }
	ul {
		li {
			position: relative;
			padding-left: 35px;
			margin-bottom: 20px;
			&:before {
				content: "";
				display: block;
				position: absolute;
				top: 5px;
				left: 0;
				width: 12px;
				height: 12px;
				@include cover;
				background-image: url("data:image/svg+xml;base64,"); } } } }



